import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root',
})
export class AuthService {
  loggedIn = false;
  admin = false;

  constructor() {}

  logIn(login, password) {
    // typiquement, acceptera en paramtres un login et un password
    // vrifier qu'ils sont ok, et si oui, positionner la proprit loggedIn  true
    // si login/password non valides, positionner  false;

    if (login === 'admin') this.admin = true;

    this.loggedIn = true;
  }

  logOut() {
    this.loggedIn = false;
    this.admin = false;
  }

  // exemple d'utilisation :
  // isAdmin.then(admin => { console.log("administrateur : " + admin);})
  isAdmin() {
    return new Promise((resolve, reject) => {
      resolve(this.admin);
    });
  }
}
